@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
:root {
  --font-family: 'Manrope', sans-serif;

  --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
  
  --color-bg: #040C18;
  --color-blog: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
  --color-button: #51c404;;
  
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  background: var(--color-bg);
}

a {
  color: unset;
  text-decoration: none;
}

.gradient__bg {

  /* safari 5.1+,chrome 10+ */

  /* opera 11.10+ */

  /* ie 10+ */

  /* global 92%+ browsers support */
  background:radial-gradient(circle at 3% 3%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 50%);
}

.gradient__text {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.section__padding {
  padding: 4rem 6rem;
}

.section__margin {
  margin: 4rem 6rem;
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@media screen and (max-width: 700px) {
  .section__padding {
    padding: 4rem;
  }

  .section__margin {
    margin: 4rem;
  }
}

@media screen and (max-width: 550px) {
  .section__padding {
    padding: 4rem 2rem;
  }

  .section__margin {
    margin: 4rem 2rem;
  }  
}
.og__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.og__navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.og__navbar-links_logo {
    margin-right: 2rem;
}

.og__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.og__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.og__navbar-links_container a,
.og__navbar-sign a,
.og__navbar-menu_container a {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.og__navbar-sign button,
.og__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: var(--color-button);
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
}

.og__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.og__navbar-menu svg {
    cursor: pointer;
}

.og__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.og__navbar-menu_container a {
    margin: 1rem 0;
}

.og__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .og__navbar-links_container {
        display: none;
    }

    .og__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .og__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .og__navbar {
        padding: 2rem;
    }

    .og__navbar-sign {
        display: none;
    }

    .og__navbar-menu_container {
        top: 20px;
    }

    .og__navbar-menu_container-links-sign {
        display: block;
    }
}

svg {
    position:relative; 
  }
  
.og-image OgImg {
    width: 100%;
    height: 100%;

}

.og-image a {
  position:relative;
  display: block;
  width:100%;
  height:100%;
}

.og__balance {
    display: flex;
    flex-direction: column;    
}

.og__balance-heading {
    width: 100%;
    text-align: center;

    margin-bottom: 5rem;
}

.og__balance-heading h1 {
    font-size: 62px;
    line-height: 75px;
    font-family: var(--font-family);
    font-weight: 800;
}
 
.og__balance-container {
    display: flex;
    flex-direction: column-reverse;
    flex: 1 1;

    display:grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
    .og__balance-container {
        flex-direction: column-reverse;
        grid-template-columns: repeat(2, 1fr);
}


    /* .og__balance-container_groupA {
        margin: 2rem 0;
    }

    .og__balance-container_groupA .og__balance-container_article  {
       width: 48%;
    }
*/

}

@media screen and (max-width: 300px) {
    .og__balance-container {
        grid-template-columns: repeat(1, 1fr);
    }

    /* .og__balance-container_groupA .og__balance-container_article  {
       width: 100%;
    } */

    .og__balance-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .og__balance-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

}
.og__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.og__footer-heading {
    width: 100%;
    text-align: center;

    margin-bottom: 3rem;
}

.og__footer-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 64px;
    line-height: 75px;
}

.og__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.og__footer-copyright p {
    /* font-size: 18px;
    font-family: var(--font-family);
    line-height: 15px;
    color: #fff; */
    font-family: var(--font-family);
    font-weight: 400;
    color: var(--color-text);
}

.og__footer-avatar {
    padding: 1rem;
    height: 150px;
    width: 150px;
    border-radius: 50px;
}

@media screen and (max-width: 850px) {
    .og__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .og__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

}

@media screen and (max-width: 400px) {
    .og__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}

.og__header {
    display: flex;
    flex-direction: row;
}

.og__header-image {
    flex: 1 1;
    justify-content: center;
    align-self: center;
}

.og__header-content {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 5rem;
}

.og__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.og__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--color-text);

    margin-top: 1.5rem;
}

.og__header-content .smalltext {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
    margin-top: 0.5rem;
}

.og__header-content__input button {
    flex: 0.6 1;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    background: var(--color-button);
    border: 2px solid var(--color-button);
    padding: 0 1rem;
    color: #fff;
    cursor: pointer;
    outline: none;

    border-radius: 5px;
}



@media screen and (max-width: 1050px) {
    .og__header {
        flex-direction: column;
    }

    .og__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .og__header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .og__header-content p {
        font-size: 16px;
        line-height: 24px;
    }
    
    .og__header-content__people {
        flex-direction: column;
    }

    .og__header-content__people p {
        margin: 0;
    }

    .og__header-content__input button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .og__header-content h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .og__header-content p {
        font-size: 14px;
        line-height: 24px;
    }

    .og__header-content__input button {
        font-size: 12px;
        line-height: 16px;
    }


}

@media screen and (min-width: 1500px) {

    /* make the text part wider to prevent the freatured OG from getting to prominent */
    .og__header-content {
        flex: 2 1;
    }
    
}

.og__rules {
