@media screen and (max-width: 490px) {
    .og__rules-content h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .og__rules-content p {
        font-size: 14px;
        line-height: 24px;
    }

    .og__rules-content__input button {
        font-size: 12px;
        line-height: 16px;
    }
